<template>
  <div>
    <b-modal id="user-info" hide-footer no-close-on-backdrop hide-header dialog-class="modal-425px" body-class="p-3 bg-soft-light rounded">
      <b-form @submit.prevent="onSubmit" ref="form">
        <b-card class="mb-0 rounded-sm" body-class="p-5">
          <b-form-group label="What's your name?" label-class="font-size-lg">
            <b-input-group class="input-group-merge border-merge">
              <b-form-input size="lg" v-model="name" type="text" placeholder="Enter your name" required class="border-left-0 border-right-0 border-top-0 rounded-0 " max="20" />
              <b-input-group-prepend>
                <b-input-group-text class="p-0 rounded-0 border-left-0 border-top-0 border-right-0">
                  <Avatar :username="name.trim() ? name : 'your name'" :rounded="false" :size="36.5" :customStyle="{ 'border-radius': '0.325rem' }" />
                </b-input-group-text>
              </b-input-group-prepend>
            </b-input-group>
          </b-form-group>
          <b-button block class="py-2" type="submit" :disabled="!name.trim().length" variant="primary">Join the room</b-button>
        </b-card>
      </b-form>
    </b-modal>
  </div>
</template>
<script>
  import Avatar from "vue-avatar";
  export default {
    components: { Avatar },
    data() {
      return { name: "" };
    },
    methods: {
      // Emit the given name.
      onSubmit() {
        this.$emit("guestName", this.name);
        this.$bvModal.hide("user-info");
      },
    },
  };
</script>
<style>
  .modal-425px {
    width: 425px !important;
  }
</style>
